<template>
    <div>
        <el-button @click="dialogVisible = true" size="mini"></el-button>
        <el-dialog  top="10px"  :visible.sync="dialogVisible"  width="80%"  title="" :modal-append-to-body="false">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" label-width="100" class="search-form"  :inline="true">
                    <el-form-item  label="(/)" >
                        <el-input v-model="searchForm.keywords"></el-input>
                    </el-form-item>
                    <el-form-item  label="ID" >
                        <el-input v-model="searchForm.user_id"></el-input>
                    </el-form-item>
                    <el-form-item  label="">
                        <el-select v-model="searchForm.agent_type" placeholder="">
                            <el-option label="" :value=0></el-option>
                            <el-option label="" :value=1></el-option>
                            <el-option label="" :value=2></el-option>
                            <el-option label="" :value=3></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item>
                        <el-button icon="el-icon-search" @click="getList(1)" size="mini" type="primary"></el-button>
                    </el-form-item>
                </el-form>
                <el-table size="mini" :data="datas.list" height="500" style="width: 100%" >
                    <el-table-column prop="user_id" label="ID">
                        <template slot-scope="scope">
                            <p>ID<span>{{scope.row.user_id}}</span></p>
                        </template>
                    </el-table-column>

                    <el-table-column label="" >
                        <template slot-scope="scope">
                            <div class="ellipsis-02">
                                {{scope.row.nickname}}
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column label="">
                        <template slot-scope="scope">
                            <img width="40" :src="scope.row.face"/>
                        </template>
                    </el-table-column>

                    <el-table-column prop="mobile" label="" ></el-table-column>

                    <el-table-column label="" >
                        <template slot-scope="scope">
                            {{scope.row.agent_group_mean}}
                        </template>
                    </el-table-column>

                    <el-table-column label="">
                        <template slot-scope="scope">
                            <el-button type="text" @click="puserSelect(scope.row)" ></el-button>
                        </template>
                    </el-table-column>

                </el-table>
                <div class="page">
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from "@/api";
    import DateBetween from "../../../components/DateBetween";


    export default {
        name: 'AgentSelect',
        components: {DateBetween},
        props:[],
        data() {
            return {
                dialogVisible:false,
                //
                loading: false,
                //
                datas: [],
                //
                searchForm: this.initSearch(),
            }
        },
        watch:{
        },
        created() {
            this.getList();
        },
        methods: {
            initSearch(){
                return {
                    date:[],
                    keywords:'',
                    user_id:'',
                    agent_type: 0,
                }
            },

            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.agent.agent/getAgentList", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    end_add_time: this.searchForm.date[1] ? this.searchForm.date[1] :  0,
                    bg_add_time: this.searchForm.date[0] ? this.searchForm.date[0] : 0,
                    keywords: this.searchForm.keywords,
                    user_id: this.searchForm.user_id,
                    agent_type: this.searchForm.agent_type,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },


            puserSelect(val){
                this.dialogVisible = false;
                this.$emit("puserSelect",val);
            },
        }
    }
</script>

