<template>
    <!---->
    <div>
        <div  v-loading="loading">
            <el-table :data="datas.list" style="width: 100%" size="small">
                <el-table-column prop="address_id" label="ID" width="100"></el-table-column>
                <el-table-column prop="name" label="" width="100"></el-table-column>
                <el-table-column prop="mobile" label="" width="100"></el-table-column>
                <el-table-column prop="province" label="" width="150"></el-table-column>
                <el-table-column prop="city" label="" width="150"></el-table-column>
                <el-table-column prop="division" label="/" width="150"></el-table-column>
                <el-table-column prop="address" label=""></el-table-column>
            </el-table>
        </div>
    </div>
</template>
<script>
    import * as api from "@/api";
    export default {
        name: "UserGoodsAddress",
        props:['user_id'],
        data() {
            return {
                loading:false,
                datas:{},
            }
        },
        created(){
            this.getList();
        },
        methods: {
            getList(page=1){
                this.loading = true;
                api.post("/shop/admin.user.userview/address", {
                    appToken: this.$store.getters.appToken,
                    user_id:this.user_id,
                    page:page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
    }
</script>
