<template>
    <div>
        <top-nav title=""></top-nav>
        <div class="wyu-main" v-loading="loading.loading">
            <div style="min-width: 1600px">
                <div class="wyu-block">
                    <el-row :gutter="10" style="height: 260px;padding: 20px">
                        <el-col :span="8">
                            <div class="user-view-item" style="height: 220px;padding: 50px 40px 40px 40px">
                                <el-col :span="8">
                                    <img width="120" height="120" :src="userDetail.face">
                                </el-col>
                                <el-col :span="8">
                                    <p style="color: #333333;font-weight:500;">
                                        {{userDetail.nickname}}(ID:{{$route.params.id}})</p>
                                    <div class="user-view-item-label"><span>{{userDetail.mobile}}</span></div>
                                    <div class="user-view-item-label"><span>{{userDetail.sex_mean}}</span></div>
                                    <div class="user-view-item-label"><span>{{userDetail.birthday}}</span></div>
                                </el-col>
                                <el-col :span="8"
                                        style="height: 100%; justify-content: flex-end; display: flex; align-items: center;">
                                    <el-button size="mini" @click="dialog.userDialog=true"></el-button>
                                </el-col>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item">
                                <div class="user-view-item-title"><span></span>
                                    <el-dropdown style="float: right">
                                    <span class="el-dropdown-link user-view-item-title-btn"><i
                                         class="el-icon-arrow-down el-icon--right"></i>
                                    </span>
                                    <el-dropdown-menu split-button slot="dropdown" size="mini">
                                            <el-dropdown-item><span @click="dialog.moneyDialog = true"></span>
                                            </el-dropdown-item>
                                            <el-dropdown-item><span @click="dialog.jifenDialog = true"></span>
                                            </el-dropdown-item>
                                        </el-dropdown-menu>
                                    </el-dropdown>
                                </div>
                                <div class="user-view-item-label"><span></span><span style="color: red">{{userDetail.money}}</span>
                                </div>
                                <div class="user-view-item-label"><span></span><span style="color: red">{{userDetail.integral}}</span>
                                </div>
                            </div>
                            <div class="user-view-item" style="margin-top: 20px">
                                <div class="user-view-item-title"><span>PLUS</span><span
                                        class="user-view-item-title-btn" @click="dialog.vipDialog = true"></span>
                                </div>
                                <div class="user-view-item-label"><span></span>
                                    <el-tag size="mini" type="info" v-if="userDetail.is_plus_vip == 0"></el-tag>
                                    <el-tag size="mini" type="success" v-if="userDetail.is_plus_vip == 1"></el-tag>
                                </div>
                                <div class="user-view-item-label"><span></span>
                                    <span v-if="userDetail.is_plus_vip == 1">{{userDetail.plus_vip_time}}</span>
                                    <span v-if="userDetail.is_plus_vip == 0">--</span>
                                </div>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item">
                                <div class="user-view-item-title"><span></span><span
                                        class="user-view-item-title-btn" v-if="userDetail.is_agent == 0" @click="dialog.agentDialog=true"></span></div>
                                <div class="user-view-item-label"><span></span>
                                    <el-tag size="mini" type="success" v-if="userDetail.is_agent == 1"></el-tag>
                                    <el-tag size="mini" type="info" v-if="userDetail.is_agent == 0"></el-tag>
                                </div>
                                <div class="user-view-item-label"><span>&nbsp;&nbsp;&nbsp;</span><span>{{userDetail.agent_group_mean}}</span>
                                </div>
                            </div>
                            <div class="user-view-item" style="margin-top: 20px">
                                <div class="user-view-item-title"><span></span></div>
                                <div class="user-view-item-label"><span></span><span
                                        style="display: inline-block;max-width: 130px" class="ellipsis-01">{{userDetail.pid_name}}</span><span
                                        v-if="userDetail.pid > 0">ID:{{userDetail.pid}}</span>
                                    <el-button v-if="userDetail.pid == 0" size="mini" @click="dialog.puserDialog = true"></el-button>
                                </div>
                                <div class="user-view-item-label"><span></span><span
                                        style="display: inline-block;max-width: 130px" class="ellipsis-01">{{userDetail.pid2_name}}</span><span
                                        v-if="userDetail.pid > 0">ID:{{userDetail.pid2}}</span></div>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item">
                                <div class="user-view-item-title"><span></span><span
                                        class="user-view-item-title-btn" @click="dialog.couponDialog = true"></span></div>
                                <div class="user-view-item-label"><span></span><span>{{userDetail.order_num}}</span>
                                </div>
                                <div class="user-view-item-label">
                                    <span></span><span>{{userDetail.coupon_num}}</span></div>
                            </div>
                            <div class="user-view-item" style="margin-top: 20px">
                                <div class="user-view-item-title"><span></span></div>
                                <div class="user-view-item-label"><span></span>{{userDetail.is_lock == 1 ? '' :
                                    ''}}<span style="margin-left: 10px">
                                    <el-switch @change="lockUser" v-loading="loading.lockLoading"
                                               v-model="userDetail.is_lock" :active-value="1"
                                               :inactive-value="0"></el-switch>
                                </span>
                                </div>
                            </div>
                        </el-col>
                        <el-col :span="4">
                            <div class="user-view-item">
                                <div class="user-view-item-title"><span></span></div>
                                <div class="user-view-item-label"><span></span><span>{{userDetail.add_time}}</span>
                                </div>
                                <div class="user-view-item-label"><span></span><span>{{userDetail.device}}</span>
                                </div>
                            </div>
                            <div class="user-view-item" style="margin-top: 20px">
                                <div class="user-view-item-title"><span></span></div>
                                <div class="user-view-item-label">
                                    <span></span><span>{{userDetail.last_time}}</span></div>
                                <div class="user-view-item-label">
                                    <span></span><span>{{userDetail.last_device}}</span></div>
                            </div>
                        </el-col>
                    </el-row>
                </div>
            </div>
            <div class="wyu-block" style="margin-top: 20px">
                <user-view-log :user_id="$route.params.id"></user-view-log>
            </div>
        </div>
        <!---->
        <el-dialog title="" :modal="false" :visible.sync="dialog.userDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" label-width="80px" style="padding: 0" v-loading="loading.userInfoLoading">
                <el-form-item label="">
                    <img-uploads w="100" h="100" :img="userDetail.face" v-model="userDetail.face"></img-uploads>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="userDetail.mobile"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="userDetail.nickname"></el-input>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="userDetail.sex">
                        <el-option label="" :value="0"></el-option>
                        <el-option label="" :value="1"></el-option>
                        <el-option label="" :value="2"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-date-picker
                            v-model="userDetail.birthday"
                            value-format="yyyy/MM/dd"
                            type="date"
                            placeholder="">
                    </el-date-picker>
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.userDialog = false;getDetail()"> </el-button>
                    <el-button size="mini" type="primary" @click="editUserIfo"> </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="dialog.moneyDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" label-width="80px" style="padding: 0" v-loading="loading.moneyLoading">
                <el-form-item label="">
                    <span style="color: red">{{userDetail.money}}</span>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="moneyForm.type" placeholder="">
                        <el-option v-for="(val,key) in status.money_status" :key="key" :label="val.value"
                                   :value="val.key"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="moneyForm.money"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.moneyDialog = false;moneyForm=initMoneyForm()"> </el-button>
                    <el-button size="mini" type="primary" @click="editUserMoney"> </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="dialog.jifenDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" label-width="80px" style="padding: 0" v-loading="loading.jifenLoading">
                <el-form-item label="">
                    <span style="color: red">{{userDetail.integral}}</span>
                </el-form-item>
                <el-form-item label="">
                    <el-select v-model="jifenForm.type" placeholder="">
                        <el-option v-for="(val,key) in status.integral_status" :key="key" :label="val.value"
                                   :value="val.key"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="">
                    <el-input v-model="jifenForm.integral"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.jifenDialog = false;jifenForm=initJifenForm()"> </el-button>
                    <el-button size="mini" type="primary" @click="editJIfenMoney"> </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!--plus-->
        <el-dialog title="PLUS" :visible.sync="dialog.vipDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form size="mini" label-width="80px" style="padding: 0" v-loading="loading.jifenLoading">

                <el-form-item label="">
                    <el-switch v-model="vipForm.is_vip" :inactive-value="0" :active-value="1"></el-switch>
                </el-form-item>

                <el-form-item label="" v-if="vipForm.is_vip == 1">
                    <el-date-picker
                            v-model="vipForm.vip_time"
                            value-format="yyyy/MM/dd"
                            type="date"
                            placeholder="">
                    </el-date-picker>
                </el-form-item>

                <el-form-item>
                    <el-button size="mini" @click="dialog.vipDialog = false;vipForm=initVipForm()"> </el-button>
                    <el-button size="mini" type="primary" @click="editVip"> </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="dialog.agentDialog" :modal-append-to-body="false" width="500px" center>
            <div class="wyu-tips wyu-tips-info" style="margin-bottom: 20px">
                   
            </div>
            <div class="wyu-tips wyu-tips-info" >
                 
            </div>
            <span slot="footer" class="dialog-footer">
                      <el-button size="mini" @click="dialog.agentDialog = false;"> </el-button>
                      <el-button size="mini" type="primary" @click="agent"> </el-button>
            </span>
        </el-dialog>
        <!---->
        <el-dialog title="" :visible.sync="dialog.couponDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form label-width="100px" style="padding: 0" v-loading="loading.couponLoading">
                <el-form-item label="" >
                    <coupon-select @selCoupon="selCoupon"></coupon-select>
                </el-form-item>
                <el-form-item label=" " v-if="coupon">
                    {{coupon.name}}
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.couponDialog = false;loading.couponLoading = false;"> </el-button>
                    <el-button size="mini" type="primary" @click="sendCoupon()"> </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        <!---->
        <el-dialog title="" :visible.sync="dialog.puserDialog" width="500px" center
                   :modal-append-to-body="false">
            <el-form label-width="100px" style="padding: 0" v-loading="loading.puserLoading">
                <el-form-item label="" >
                    <agent-select  @puserSelect="puserSelect"></agent-select>
                </el-form-item>
                <el-form-item label="" v-if="puser">
                    {{puser.nickname}} {{puser.user_id ? '(ID'+ puser.user_id + ')' : ''}}
                </el-form-item>
                <el-form-item>
                    <el-button size="mini" @click="dialog.puserDialog = false;loading.puserLoading = false;"> </el-button>
                    <el-button size="mini" type="primary" @click="setPuser()"> </el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/api.js';
    import TopNav from "@/components/TopNav";
    import ImgUploads from "../../components/Photos";
    import UserViewLog from "./components/UserViewLog";
    import CouponSelect from "./components/Coupon";
    import AgentSelect from "./components/AgentSelect";

    export default {
        name: "travelDepart",
        components: {AgentSelect, CouponSelect, UserViewLog, ImgUploads, TopNav,},
        data: function () {
            return {
                loading: {
                    loading: false, //
                    userInfoLoading: false,
                    moneyLoading: false,
                    jifenLoading: false,
                    lockLoading: false,
                    vipLoading: false,
                    agentLoading: false,
                    couponLoading: false,
                    puserLoading: false,
                },
                //
                dialog: {
                    userDialog: false,//
                    moneyDialog: false,//
                    jifenDialog: false,
                    vipDialog: false,
                    agentDialog: false,
                    couponDialog: false,
                    puserDialog: false,
                },
                birthdaybm: false,  //
                userDetail: {},
                status: {},//
                //form
                moneyForm: this.initMoneyForm(),
                jifenForm: this.initJifenForm(),
                vipForm: this.initVipForm(),
                couponForm: {},
                coupon: {},
                puser: {},
            };
        },
        created() {
            this.getDetail();
        },
        methods: {
            //moneyform
            initVipForm() {
                return {
                    is_vip: 1,
                    vip_time: '2019/01/01',
                }
            },
            //moneyform
            initMoneyForm() {
                return {
                    type: 3,
                    money: 0,
                }
            },
            //moneyform
            initJifenForm() {
                return {
                    type: 5,
                    integral: 0,
                }
            },
            //
            agent() {
                this.loading.agentLoading = true;
                api.post("/shop/admin.user.user/agent", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                }).then(() => {
                    this.dialog.agentDialog = false;
                    this.loading.agentLoading = false;
                    this.getDetail();
                }).catch(() => {
                    this.dialog.agentDialog = false;
                    this.loading.agentLoading = false;
                });
            },
            //
            lockUser() {
                this.loading.lockLoading = true;
                api.post("/shop/admin.user.user/lock", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                }).then(() => {
                    this.loading.lockLoading = false;
                }).catch(() => {
                    this.loading.lockLoading = false;
                });
            },
            //
            editVip() {
                this.loading.vipLoading = true;
                api.post("/shop/admin.user.user/vip", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                    is_vip: this.vipForm.is_vip,
                    vip_time: this.vipForm.vip_time,
                }).then(() => {
                    this.vipForm = this.initVipForm();
                    this.dialog.vipDialog = false;
                    this.loading.vipLoading = false;
                    this.$message({
                        message: '',
                        type: 'success',
                    });
                    this.getDetail();
                }).catch(() => {
                    this.loading.vipLoading = false;
                });
            },
            //
            editJIfenMoney() {
                this.loading.jifenLoading = true;
                api.post("/shop/admin.user.user/jifen", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                    type: this.jifenForm.type,
                    integral: this.jifenForm.integral,
                }).then(() => {
                    this.jifenForm = this.initJifenForm();
                    this.dialog.jifenDialog = false;
                    this.loading.jifenLoading = false;
                    this.$message({
                        message: '',
                        type: 'success',
                    });
                    this.getDetail();
                }).catch(() => {
                    this.loading.jifenLoading = false;
                });
            },
            //
            editUserMoney() {
                this.loading.moneyLoading = true;
                api.post("/shop/admin.user.user/money", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                    type: this.moneyForm.type,
                    money: this.moneyForm.money,
                }).then(() => {
                    this.moneyForm = this.initMoneyForm();
                    this.dialog.moneyDialog = false;
                    this.loading.moneyLoading = false;
                    this.$message({
                        message: '',
                        type: 'success',
                    });
                    this.getDetail();
                }).catch(() => {
                    this.loading.moneyLoading = false;
                });
            },
            //
            editUserIfo() {
                this.loading.userInfoLoading = true;
                api.post("/shop/admin.user.user/editInfo", {
                    appToken: this.$store.getters.appToken,
                    user_id: this.$route.params.id,
                    mobile: this.userDetail.mobile,
                    face: this.userDetail.face,
                    sex: this.userDetail.sex,
                    birthday: this.userDetail.birthday,
                    nickname: this.userDetail.nickname,
                }).then(() => {
                    this.loading.userInfoLoading = false;
                    this.dialog.userDialog = false;
                    this.$message({
                        message: '',
                        type: 'success',
                    });
                    this.getDetail();
                }).catch(() => {
                    this.loading.userInfoLoading = false;
                });
            },

            //
            getDetail() {
                var that = this;
                that.loading.loading = true;
                api.post('shop/admin.user.user/detail', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                }).then(res => {
                    that.loading.loading = false;
                    that.userDetail = res.detail;
                    this.status = res.status;
                }).catch(() => {
                    that.loading.loading = false;
                });
            },


            sendCoupon() {
                var that = this;
                that.loading.couponLoading = true;
                api.post('shop/admin.marketing.coupon/sendCoupon', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                    coupon_id: that.coupon.coupon_id,
                }).then(res => {
                    this.$message({
                        message: '',
                        type: 'success',
                    });
                    that.loading.couponLoading = false;
                    that.coupon = {};
                    that.dialog.couponDialog = false;
                }).catch(() => {
                    that.loading.couponLoading = false;
                });
            },

            setPuser() {
                var that = this;
                that.loading.puserLoading = true;
                api.post('shop/admin.user.user/setpuser', {
                    appToken: that.$store.getters.appToken,
                    user_id: that.$route.params.id,
                    pid: that.puser.user_id,
                }).then(res => {
                    this.$message({
                        message: '',
                        type: 'success',
                    });
                    that.loading.puserLoading = false;
                    that.puser = {};
                    that.dialog.puserDialog = false;
                    this.getDetail();
                }).catch(() => {
                    that.loading.puserLoading = false;
                });
            },


            //
            selCoupon(val) {
                this.loading.couponLoading = false;
                this.coupon = val;
            },

            //
            puserSelect(val) {
                this.loading.puserLoading = false;
                this.puser = val;

            }

        }
    };
</script>
<style>
    .user-view-item {
        height: 100px;
        padding-top: 15px;
        padding-left: 20px;
        background-color: #F8F9FB;
    }

    .user-view-item-title {
        font-size: 14px;
        font-weight: 500;
        color: #333333;
    }

    .user-view-item-label {
        font-size: 12px;
        margin-top: 12px;
    }

    .user-view-item-label span:nth-child(1) {
        color: #888888;
    }

    .user-view-item-title-btn {
        color: #005DE5;
        float: right;
        margin-right: 20px;
        cursor: pointer
    }
</style>
