<template>
    <div>
        <el-button @click="dialogVisible = true" size="mini"></el-button>
        <el-dialog  top="10px"  :visible.sync="dialogVisible"  width="1500px"  title="" :modal-append-to-body="false">
            <div class="wyu-block" v-loading="loading">
                <el-form size="mini" v-model="searchForm" class="search-form"  :inline="true">
                    <el-form-item  label="" label-width="100">
                        <el-input v-model="searchForm.name"></el-input>
                    </el-form-item>
                    <el-form-item  label="ID" v-model="searchForm.coupon_id" label-width="100">
                        <el-input v-model="searchForm.coupon_id"></el-input>
                    </el-form-item>
                    <el-form-item  label="" label-width="100">
                        <el-select v-model="searchForm.coupon_type" placeholder="">
                            <el-option :key=-999 label="" :value=-999></el-option>
                            <el-option v-for="item in datas.couponType" :key="item.key" :label="item.value" :value="item.key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item  label="" label-width="100">
                        <el-select v-model="searchForm.userarea" placeholder="">
                            <el-option :key=-999 label="" :value=-999></el-option>
                            <el-option v-for="item in datas.useArea" :key="item.key" :label="item.value" :value="item.key"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item  label="" label-width="100">
                        <product-category :no-add="true" :p-cat-id="searchForm.p_cat_id" :cat-id="searchForm.cat_id" @selCat="selCat"></product-category>
                    </el-form-item>
                    <el-form-item label="">
                        <date-between  v-model="searchForm.date"></date-between>
                    </el-form-item>
                    <el-form-item>
                        <el-button icon="el-icon-search" @click="getList(1)" size="mini" type="primary"></el-button>
                    </el-form-item>

                </el-form>
                <el-table :data="datas.list" style="width: 100%" size="small">
                    <el-table-column prop="coupon_id" label="ID" width="130"></el-table-column>

                    <el-table-column label="" >
                        <template slot-scope="scope">
                            <div class="ellipsis-02">
                                {{scope.row.name}}
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column prop="userarea" label="" width="200">
                        <template slot-scope="scope">
                            <p>{{scope.row.userarea_mean}}</p>
                            <p v-if="scope.row.userarea > 0">{{scope.row.cat_name}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column prop="type_mean" label="" width="150"></el-table-column>

                    <el-table-column label="" width="150">
                        <template slot-scope="scope">
                            <p>: {{scope.row.expire_start_time}}</p>
                            <p>: {{scope.row.expire_end_time}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column width="60">
                        <template slot-scope="scope">
                            <el-button type="text" @click="selCoupon(scope.row)" ></el-button>
                        </template>
                    </el-table-column>

                </el-table>
                <div class="page">
                    <div class="page-tool"></div>
                    <div class="page-mian">
                        <el-pagination @current-change="getList" :page-size="datas.limit"
                                       layout="total, prev, pager, next" :total="datas.count">
                        </el-pagination>
                    </div>
                </div>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    import * as api from "@/api";
    import DateBetween from "../../../components/DateBetween";
    import ProductCategory from "../components/ProductCategory";

    export default {
        name: 'CouponSelect',
        components: {ProductCategory, DateBetween},

        data() {
            return {
                dialogVisible:false,
                //
                loading: false,
                //
                datas: [

                ],
                //
                searchForm: this.initSearch(),
                //
                goods_ids: [],
            }
        },
        created() {
            this.getList();
        },
        methods: {
            initSearch(){
                return {
                    coupon_id: '',
                    coupon_type: -999,
                    use_area: -999,
                    cat_id:0,
                    p_cat_id:0,
                    date:[],
                }
            },

            getList(page = 1) {
                this.loading = true;
                api.post("/shop/admin.marketing.coupon/index", {
                    appToken: this.$store.getters.appToken,
                    page: page,
                    coupon_id:this.searchForm.coupon_id,
                    coupon_type:this.searchForm.coupon_type,
                    use_area:this.searchForm.use_area,
                    cat_id:this.searchForm.cat_id,
                    p_cat_id:this.searchForm.p_cat_id,
                    end_add_time:this.searchForm.date[1] ? this.searchForm.date[1] :  0,
                    bg_add_time:this.searchForm.date[0] ? this.searchForm.date[0] : 0,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch((error) => {
                    this.loading = false;
                });
            },

            selCat(val){
                this.searchForm.cat_id = val.cat_id;
                this.searchForm.p_cat_id = val.p_cat_id;
            },

            selCoupon(val){
                this.dialogVisible = false;
                this.$emit("selCoupon",val);
            },


        }
    }
</script>

