<template>
    <!---->
    <div >
        <div  v-loading="loading">
            <el-table :data="datas.list" style="width: 100%" size="small">
                <el-table-column prop="goods_id" label="ID" width="80"></el-table-column>
                <el-table-column  label="" width="100">
                    <template slot-scope="scope">
                        <img width="50" :src="scope.row.goods_photo"/>
                    </template>
                </el-table-column>
                <el-table-column prop="goods_name" label=""></el-table-column>
                <el-table-column prop="goods_sn" label=""></el-table-column>
                <el-table-column prop="spec_key_name" label=""></el-table-column>
                <el-table-column prop="add_time" label=""></el-table-column>

                <el-table-column  label="">
                    <template slot-scope="scope">
                        <el-tag size="mini" type="info" v-if="scope.row.is_delete == 1"></el-tag>
                        <el-tag size="mini" v-if="scope.row.is_delete == 0" ></el-tag>
                        <p v-if="scope.row.is_delete == 1">{{scope.row.delete_time}}</p>
                    </template>
                </el-table-column>
                <el-table-column  label="">
                    <template slot-scope="scope">
                        <el-tag size="mini" type="info" v-if="scope.row.is_pay == 1"></el-tag>
                        <el-tag size="mini" v-if="scope.row.is_pay == 0" ></el-tag>
                        <p v-if="scope.row.is_pay == 1">{{scope.row.pay_time}}</p>
                    </template>
                </el-table-column>

            </el-table>
            <div style="text-align: right" class="block">
                <el-pagination @current-change="getList" :page-size="datas.limit"
                               layout="total, prev, pager, next" :total="datas.count">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from "@/api";
    export default {
        name: "UserGoodsCart",
        props:['user_id'],
        data() {
            return {
                loading:false,
                datas:{},
            }
        },
        created(){
            this.getList();
        },
        methods: {
            getList(page=1){
                this.loading = true;
                api.post("/shop/admin.user.userview/cart", {
                    appToken: this.$store.getters.appToken,
                    user_id:this.user_id,
                    page:page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
    }
</script>
<style scoped>

</style>
