<template>
    <!---->
    <div >
        <div  v-loading="loading">
            <el-table :data="datas.list" style="width: 100%" size="small">
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <table>
                            <tr v-for="val in  props.row.goods">
                                <td>{{val.goods_name}}</td>
                                <td></td>
                                <td>{{val.spec_key_name}}</td>
                            </tr>
                        </table>
                    </template>
                </el-table-column>
                <el-table-column width="100" prop="order_id" label=""></el-table-column>
                <el-table-column prop="add_time" label=""></el-table-column>
                <el-table-column prop="give_integral" label=""></el-table-column>
                <el-table-column prop="order_status_mena" label=""></el-table-column>
                <el-table-column prop="pay_status_mean" label=""></el-table-column>
                <el-table-column prop="shipping_status_mena" label=""></el-table-column>
            </el-table>
            <div style="text-align: right" class="block">
                <el-pagination @current-change="getList" :page-size="datas.limit"
                               layout="total, prev, pager, next" :total="datas.count">
                </el-pagination>
            </div>
        </div>
    </div>
</template>
<script>
    import * as api from "@/api";
    export default {
        name: "UserOrderLog",
        props:['user_id'],
        data() {
            return {
                loading:false,
                datas:{},
            }
        },
        created(){
            this.getList();
        },
        methods: {
            getList(page=1){
                this.loading = true;
                api.post("/shop/admin.user.userview/order", {
                    appToken: this.$store.getters.appToken,
                    user_id:this.user_id,
                    page:page,
                }).then(res => {
                    this.datas = res;
                    this.loading = false;
                }).catch(() => {
                    this.loading = false;
                });
            },
        },
    }
</script>
<style scoped>

</style>
